@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;700;900&display=swap");
* {
  margin: 0;
  padding: 0;
}
nav {
  background-color: black;
  color: white;
  padding: 10px;
  text-align: center;
}

.container {
  background-color: rgba(173, 160, 175, 0.871);
  width: 70%;
  margin: auto;
  min-height: 100vh;
  padding: 4vmax 8vmax;
  box-sizing: border-box;
}

.container > form {
  display: flex;
  flex-direction: column;
}

input,
textarea,
button {
  padding: 20px;
  border: 1px solid black;
  border-radius: 5px;
  margin: 10px 0;
  font-size: 1.2rem;
}
.container > h1 {
  text-align: center;
  margin: 30px;
  font-size: 2.5rem;
}

.task {
  display: flex;
  align-items: center;
  background-color: grey;
  padding: 20px;
  border-radius: 5px;
  margin: 20px 0;
}

.task div {
  width: 100%;
}
.task button {
  border-radius: 100%;
  height: 30px;
  width: 30px;
  font-size: 1.6rem;
  display: flex;
  justify-content: center;
  background-color: crimson;
  align-items: center;
}
.task button:hover {
  background-color: red;
}
button {
  border: none;
  cursor: pointer;
  background-color: #262626;
  color: white;
}
